import * as React from 'react';
import Video from './video';
import { BiPluginConfig } from '../../../types';

export default function createRenderNode(config: BiPluginConfig) {
  const videoConfig = config.video || {};
  const {
    locale: videoLocaleConfig = {},
    downloadFile,
    onMediaLoadSuccess,
    onMediaLoadError,
    transformVideoURL,
    setTempPosterUrl,
    renderVideoMask,
    allowDownload,
    onPreview,
    allowFullscreen,
  } = videoConfig;

  return {
    embed: (props, controller, next) => {
      const { node } = props;

      if (node.data.type === 'video' && node.data.src) {
        return (
          <Video
            {...props}
            controller={controller}
            onDownload={downloadFile}
            locale={videoLocaleConfig}
            onMediaLoadSuccess={onMediaLoadSuccess}
            onMediaLoadError={onMediaLoadError}
            transformVideoURL={transformVideoURL}
            setTempPosterUrl={setTempPosterUrl}
            renderVideoMask={renderVideoMask}
            allowDownload={allowDownload}
            onPreview={onPreview}
            allowFullscreen={allowFullscreen}
          />
        );
      }
      return next();
    },
  };
}
